.gutenify--box-shadow-control {
	margin: 10px 0;
	label {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}
.components-button.is-secondary {
	&.gutenify--box-shadow-control--button {
		color: #000;
		box-shadow: inset 0 0 0 1px #000;
		&.gutenify--box-shadow-control--button--active {
			box-shadow: inset 0 0 0 1px var(--wp-admin-theme-color);
			color: var(--wp-admin-theme-color);
		}
	}
}

.gutenify--box-shadow-control-content
	.components-popover__content
	> div {
	padding: 15px;
}
