/* elements
--------------------------------------------- */

.woocommerce {
	#respond input#submit,
	a.button,
	button.button,
	input.button,
	#respond input#submit.alt,
	a.button.alt,
	button.button.alt,
	input.button.alt {
		border-radius: 0;
		font-weight: 400;
		padding: 10px 25px;
	}
	table.shop_attributes td,
	table.shop_attributes th {
		border: 1px solid #ddd;
	}

	div.product .woocommerce-tabs .panel {
		border: 1px solid #d3ced2;
		border-top: none;
		margin-top: -27px;
		padding: 30px;
	}
	.woocommerce-message {
		border-top-color: #000;
	}

	.woocommerce-message::before {
		color: #222;
	}

	 .quantity .qty {
		padding: 6px;
		text-align: center;
		width: 3.631em;
	}

	div.product form.cart div.quantity {
		float: left;
		margin: 0 10px 0 0;
	}

	 ul.products li.product .woocommerce-loop-category__title,
	 ul.products li.product .woocommerce-loop-product__title,
	 ul.products li.product h3 {
		font-size: 1.2rem;
		margin: 0;
		padding: 0.5em 0 10px;
	}

	 ul.products li.product .price {
		color: #000;
		display: block;
		font-size: 1rem;
		font-weight: 400;
		margin-bottom: 0.5em;
	}

	 ul.products li.product,
	.woocommerce-page ul.products li.product {
		padding: 0 0 30px 0;
		text-align: center;
	}
	ul.products li.product a img {
		background: #f1f1f1;
	}

	 ul.products li.product .star-rating {
		color: #ff9800;
		margin: 0 auto 10px;
	}

	section.related.products h2 {
		margin-bottom: 30px;
	}

	 div.product p.price,
	div.product span.price {
		color: #000;
	}

	.woocommerce-breadcrumb {
		border: 2px solid #f8f8f8;
		padding: 10px 30px;
		text-align: left;
		margin-bottom: 50px;
	}
	.star-rating span {
			color: #ffa500;
		}
	span.onsale {
		background: #ffa500;
	}
	ul.products li.product span.onsale {
		background: #ffa500;
		border: none;
		border-radius: 0;
		color: #fff;
		font-size: 14px;
		padding: 3px 12px;
		position: absolute;
		right: 0;
		top: 14px;
		transform: rotate(89deg);
		z-index: 9;
		margin: 0;
		line-height: inherit;
		min-width: auto;
		min-height: auto;
	}
	 .quantity .qty {
		width: 5rem;
		text-align: center;
		padding: 3px 0;
		border: 1px solid #ddd;
		border-radius: 0;
	}
	div.product p.price,
	div.product span.price {
		color: #000000;
		font-size: 1.25em;
	}
	figure.woocommerce-product-gallery__wrapper {
		border: 1px solid #ddd;
	}

}

.wc-block-grid__product {
	overflow: hidden;
	.wc-block-grid__product-image img {
		background: #ddd;
		height: auto;
		max-width: 100%;
		width: auto;
	}

	a {
		text-decoration: inherit;
	}
	.wc-block-grid__product-onsale {
		background: #ffa500;
		border: none;
		border-radius: 0;
		color: #fff;
		font-size: 14px;
		padding: 3px 12px;
		position: absolute;
		right: 0;
		top: 14px;
		transform: rotate(89deg);
		z-index: 9;
	}
	.wc-block-grid__product-price.price span.woocommerce-Price-amount.amount {
		font-size: 1rem;
		font-weight: 400;
		color:#000;
	}
	.wc-block-grid__product-image,
	.wc-block-grid__product-title {
		font-size: 1.1rem;
		font-weight: 500;
		margin: 0 0 12px;
	}
	ins {
		background: none;
	}
}
.wc-block-grid__product-add-to-cart {
	&.wp-block-button {
		.wp-block-button__link {
			border: none;
			font-weight: var(--wp--custom--core-button--typography--font-weight);
			font-size: var(--wp--custom--core-button--typography--font-size);
			padding-left: var(--wp--custom--core-button--spacing--padding--left);
			padding-right: var(--wp--custom--core-button--spacing--padding--right);
			padding-top: var(--wp--custom--core-button--spacing--padding--top);
			padding-bottom: var(--wp--custom--core-button--spacing--padding--bottom);
		}
	}
}

.wp-block-woocommerce-product-categories {
	ul.wc-block-product-categories-list {
		margin: 0;
		padding: 0;
		li {
			background: #f1f1f1;
			border-bottom: 1px solid #ddd;
			display: block;
			list-style: none;
			margin: 0;
			padding: 10px 25px;
			a {
				text-decoration: none !important;
			}
		}
	}
}

@media screen and (max-width: 767px) {
	.woocommerce ul.products[class*="columns-"] li.product,
	.woocommerce-page ul.products[class*="columns-"] li.product {
		margin: 0 0 15px 0;
		width: 100%;
	}
}
//twenty two theme
.woocommerce .wp-site-blocks>.wp-block-group {
    max-width: inherit;
    margin-left: auto;
    margin-right: auto;
}

// cart block style

	.wc-block-mini-cart__button:hover,
	.wc-block-mini-cart__button:focus,
	.wc-block-mini-cart__button:active {
		background: transparent;
		color: inherit;
		font-size: 17px;
		cursor: pointer;
	}
	.wc-block-mini-cart__icon {
		display: block;
		height: 20px;
		width: 20px;
	}
	.wc-block-mini-cart__badge {
		padding: 0 2px;
		min-width: 17px;
		height: 17px;
		font-size: 14px;
		min-height: auto;
		margin-left: -7px;
		width: auto;
	}
	.wc-block-cart-item__wrap {
		text-align: left;
	}
	.is-medium table.wc-block-cart-items .wc-block-cart-items__row, .is-mobile table.wc-block-cart-items .wc-block-cart-items__row, .is-small table.wc-block-cart-items .wc-block-cart-items__row {
		padding: 20px;
		grid-template-columns: 1fr 1fr;
	}
	.wc-block-components-quantity-selector {
		float: left;
		margin-right: 15px;
	}
	table.wc-block-cart-items .wc-block-cart-items__row .wc-block-cart-item__quantity .wc-block-cart-item__remove-link {
		font-size: 16px;
		color: #e00505;
		cursor: pointer;
	}
	.wc-block-components-quantity-selector>.wc-block-components-quantity-selector__button--plus,
	.wc-block-components-quantity-selector>.wc-block-components-quantity-selector__button--minus,
	.wc-block-components-quantity-selector {
		border-radius: 0;

	}
	.wc-block-components-product-price__value.is-discounted {
		margin-left: 0.5em;
		background: no-repeat;
		font-weight: 600;
	}

	.wc-block-mini-cart__drawer .components-modal__content {
		padding: 30px;
		position: relative;
	}
	.wc-block-components-product-badge {
		font-size: 14px;
		background: #e65100;
		color: #fff;
		border: none;
		text-transform: capitalize;
	}
	.is-mobile table.wc-block-cart-items .wc-block-cart-items__row .wc-block-cart-item__quantity {
		margin: 0;
		padding: 0;
	}
	h2.wc-block-mini-cart__title {
		font-size: 1.5rem;
	}
	.wc-block-components-drawer .components-modal__header .components-button {
		background:#000;
		color: #fff;
		top: 32px;
		height: auto;
		width: unset;
		padding: 3px 5px 0;
		border-radius: 100%;
		right: 25px;
		margin: 0;
		cursor: pointer;
	}

	.woocommerce .woocommerce-cart-form table.shop_table {
		border-right: none;
	}

	.woocommerce table.shop_table  td,
	.woocommerce table.shop_table  tr,
	.woocommerce table.shop_table  th {
		border:none;
		border-right: 1px solid #ddd;
	}
	.woocommerce table.shop_table .product-name {
		text-align: left;
		padding-left: 20px;
	}
	input#coupon_code {
		width: auto;
		padding: 2px 15px;
		border-radius: 0;
		font-size: 17px;
		background: none;
	}
	.woocommerce .woocommerce-cart-form  a.remove {
		height: auto;
		width: auto;
	}
	form.woocommerce-cart-form,
	.woocommerce .cart-collaterals .cart_totals,
	.woocommerce-page .cart-collaterals .cart_totals,
	form.checkout.woocommerce-checkout {
		padding: 40px;
		box-shadow: 0 0 26px #00000017;
		margin-bottom: 30px;
	}
	// checkout
	.checkout.woocommerce-checkout .select2-container--default .select2-selection--single .select2-selection__rendered {
		line-height: 2.4;
	}
	.checkout.woocommerce-checkout .select2-container--default .select2-selection--single .select2-selection__arrow {
		height: 39px;
		width: 40px;
	}
	.checkout.woocommerce-checkout .select2-container .select2-selection--single {
		height: auto;
		border-radius: 0;
		background: #f7f7f7;
		border: none;
	}
	.woocommerce form .woocommerce-billing-fields__field-wrapper .form-row label {
		line-height: 1.75;
		margin-bottom: 10px;
	}
	.woocommerce form .woocommerce-billing-fields__field-wrapper .form-row {
		margin: 0 0 20px;
	}
	.woocommerce .woocommerce-checkout-review-order table.shop_table {
		border-right: 0;
		border-radius: 0;
	}
	.woocommerce form .form-row .woocommerce-input-wrapper textarea{
		height: 150px;
	}
