@import "./common.scss";
@import "./fonticonpicker.css";
@import "../blocks/**/editor.scss";
@import "../extensions/**/**/editor.scss";
@import "../extend/**/**/editor.scss";
@import "../components/**/**/editor.scss";

* {
    box-sizing: border-box;
}
.editor-styles-wrapper {
	@import "./woocommerce";
}

.gutenify-editor-tab-panel {
	.components-input-control__container {
		margin-bottom: 15px;
	}
	.gutenify-editor-tab-panel {
		.components-tab-panel__tabs {
			margin-bottom: 10px;
		}

	}
	.gutenify-accordion-wrapper {
			margin-bottom: 5px;
			.components-tab-panel__tabs {
				margin-top: 0;
			}
	}
	.padding-control-wrapper {
		margin: 15px 0;
	}
	.components-tab-panel__tabs {
		align-items: stretch;
		border-bottom: none;
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
		margin-top: 15px;
	}
	.components-unit-control-wrapper {
		margin-top: 10px;
		margin-bottom: 10px;
	}
	.components-tab-panel__tabs-item {
		background: #f1f0f0;
		display: flex;
		height: auto;
		justify-content: center;
		padding: 10px 10px;
		position: relative;
		width: 100%;
	}
	.components-tab-panel__tabs-item:hover,
	.components-tab-panel__tabs-item:focus,
	.components-tab-panel__tabs-item:active,
	.components-tab-panel__tabs-item.active-tab {
		background: #0393e3;
		box-shadow: none;
		color: #fff;
		&::before {
			background: #3393e3;
			bottom: -3px;
			content: "";
			display: block;
			height: 7px;
			position: absolute;
			transform: rotate(45deg);
			width: 7px;
		}
	}

	.components-select-control
	.components-input-control__container
	.components-input-control__backdrop {
		border-color: #e0e0e0;
	}

	.components-flex-item .components-input-control__label {
		display: block;
		margin-bottom: 10px;
		margin-top:10px;
	}

	.components-color-palette__custom-color {
		height: 40px;
		text-align: center;
	}
	.components-heading + hr {
		margin-top: 0;
	}
	h2.components-heading {
		font-weight: 700;
		margin-bottom: 7px;
	}
	.components-tab-panel__tab-content {
		border: 1px solid #f1f0f0;
		padding: 10px;
		.components-tab-panel__tab-content {
			border: none;
			padding: 0;
		}
	}
}

h2.components-panel__body-title {
	background: #f7f7f7;
}

.components-panel__body.is-opened
button.components-button.components-panel__body-toggle {
	border-bottom: 1px solid #ddd !important;
}


.components-panel__body.block-editor-block-inspector__advanced {
	border-bottom: 1px solid #e0e0e0;
}

.components-panel__body {
	border-bottom: 1px solid #e0e0e0;
	border-top: 1px solid #e0e0e0;
}

.components-input-control__backdrop {
	border-color: #e0e0e0 !important;
}

.components-panel .rfipdropdown.rfipdropdown--default.fipappear-enter-done,
.components-panel .rfipdropdown {
	left: -17px !important;
	width: 200px;
}

.gutenify-template-browser {
	ul {
		display: grid;
		grid-gap: 10px;
		grid-template-areas: "gutenify-template-browser-item gutenify-template-browser-item";
		grid-template-columns: 47% 47%;
		grid-template-rows: auto;
		li.gutenify-template-browser-item {
			margin: 0;
			.gutenify-template-browser-selector {
				border: 1px solid #ddd;
				cursor: pointer;
				padding: 0;
				small {
					background: #eee;
					display: block;
					padding: 5px;
				}
			}
			img {
				height: auto;
				max-width: 100%;
				vertical-align: middle;
			}

			&.gutenify-template-browser-item-active,
			&:hover {
				.gutenify-template-browser-selector {
					border: 1px solid #3393e3;
				}
			}
		}
	}
}

.has-shadow-dark:hover {
	transform: inherit;
}

.wp-block-group .no-margin {
	margin: 0;
}

.wp-block-group .no-padding {
	padding: 0;
}

// Field styles.
.gutenify-field-error {
	color: #f30909;

	input {
		border-color: #f30909 !important;
	}
}

.wp-block-button.is-style-fill {
	a.wp-block-button__link {
		border: none;
	}
}


// Fix core component issue.
.components-notice {
	&.is-error {
		color: #191919;
	}
}

.gutenify-accordion-item input,
.gutenify-accordion-item input:hover,
.gutenify-accordion-item input:focus,
.gutenify-accordion-item input:active {
	box-shadow: none;
}


// React select
input[id^="react-select"] {
	box-shadow:none;
}



.padding-control-wrapper {
	span.components-base-control__label {
		margin-bottom: 10px;
		display: block;
	}
}
