// @import "./mixin";
.has-shadow-dark,
.has-no-hover-shadow-dark {
	box-shadow: 0 6px 30px 7px #2125471a;
	transition: 0.5s;
}

.has-shadow-dark:hover {
	box-shadow: 1px 4px 20px -2px #0003;
	transform: translateY(-8px);
}
.editor-styles-wrapper .has-shadow-dark:hover {
    transform: inherit;
}
.has-shadow-header {
	box-shadow: 0 10px 10px #0000000a;
	position: relative;
	z-index: 999;
}

.overlap-section {
	position: relative;
	z-index: 9;
}

@media screen and (min-width: 910px) {
	.no-submenu-triangle.wp-block-navigation ul ul::before {
		display: none;
	}
}

.dark-field input[type="text"],
.dark-field input[type="email"],
.dark-field input[type="url"],
.dark-field input[type="password"],
.dark-field input[type="search"],
.dark-field input[type="number"],
.dark-field input[type="tel"],
.dark-field input[type="range"],
.dark-field input[type="date"],
.dark-field input[type="month"],
.dark-field input[type="week"],
.dark-field input[type="time"],
.dark-field input[type="datetime"],
.dark-field input[type="datetime-local"],
.dark-field input[type="color"],
body .dark-field textarea,
.dark-field .wp-block-post-comments .comment-form textarea,
body .dark-field.wp-block-post-comments .comment-form input:not([type=submit]):not([type=checkbox]),
body .dark-field.wp-block-post-comments .comment-form textarea {
	background-color: #1a1818;
	color: #fff;
}

.dark-field input::-webkit-input-placeholder {

	/* Chrome/Opera/Safari */
	color: #fff;
}

.dark-field input::-moz-placeholder {

	/* Firefox 19+ */
	color: #fff;
}

.dark-field input::-ms-input-placeholder {

	/* IE 10+ */
	color: #fff;
}

.wp-block-button.is-style-fill {
	a.wp-block-button__link {
		border: none;
	}
}

.image-zoom-hover {
	overflow: hidden;
}

.image-zoom-hover img {
	margin: 0;
	-webkit-transition: -webkit-transform 1s ease;
	-moz-transition: -moz-transform 1s ease;
	transition: transform 1s ease;
	vertical-align: middle;
	width: 100%;
}

.image-zoom-hover:hover img {
	-webkit-transform: scale(1.1);
	-moz-transform: scale(1.1);
	-o-transform: scale(1.1);
	transform: scale(1.1);
	-webkit-transform-origin: center center;
	-moz-transform-origin: center center;
	-o-transform-origin: center center;
	transform-origin: center center;
}

@media screen and (max-width: 767px) {
	.mobile-hide {
		display: none;
	}
	.wp-block-social-links {
		gap:0;
		li a {
			margin: 0 10px;

		}
	}
	.mobile-alignleft {
		    text-align: left;
			display: flex!important;
			justify-content: space-between;
			align-content: center;
			flex-direction: row;
			justify-content: left!important;
	}

	.mobile-alignright {
			text-align: right;
			display: flex!important;
			justify-content: space-between;
			align-content: center;
			flex-direction: column-reverse;
			justify-content: right!important;
	}

	.mobile-aligncenter {
		align-content: space-between;
		display: grid;
		gap: 10px 0;
		justify-content: center;
		justify-items: stretch;
		max-width: 100%;
		text-align: center;
		width: 100%;
	}
	.wp-block-social-link {
		margin: 0 3px;
	}
}

.transparent-header,
.block-editor-block-list__layout .transparent-header {
    background: transparent;
    gap: 0;
    left: 0;
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 99;
}
 .block-editor-block-list__layout .transparent-header {
	top:12px;
 }
.logged-in.admin-bar .transparent-header{
	top: 32px;
}

// margin/padding

.wp-block-group .no-margin,
.wp-site-blocks .no-margin  {
	margin: 0;
}

.wp-block-group .no-margin-top,
.wp-site-blocks .no-margin-top {
	margin-top: 0;
}

.wp-block-group .no-margin-bottom,
.wp-site-blocks .no-margin-bottom {
	margin-bottom: 0;
}

.wp-block-group .no-padding,
.wp-site-blocks .no-padding{
	padding: 0;
}

.wp-block-group .no-padding-top,
.wp-site-blocks .no-padding-top {
	padding-top: 0;
}

.wp-block-group .no-padding-bottom,
.wp-site-blocks  .no-padding-bottom {
	padding-bottom: 0;
}

@media (max-width: 767px) {
	.hide-on-mobile {
		display: none !important;
	}
}
@media (min-width: 768px) and (max-width: 1024px) {
	.hide-on-tablet {
		display: none !important;
	}
}
@media (min-width: 1025px) {
	.hide-on-desktop {
		display: none !important;
	}
}
.wp-site-blocks > * + * {
	margin:0;
}




// Alignment
.is-content-justification-left {
	display: flex;
	justify-content: flex-start;
}
.is-content-justification-right {
	display: flex;
	justify-content: flex-end;
}
.is-content-justification-space-between {
	display: flex;
	justify-content: space-between;
}

.is-content-justification-center {
	display: flex;
	justify-content: center;
}

.grayscale,.grayscale img{
	opacity: 0.6;
	filter: brightness( 100% ) contrast( 100% ) saturate( 0% ) blur( 0px ) hue-rotate( 0deg );
	transition-duration: 0.4s;
}


.grayscale:hover,.grayscale:focus,.grayscale:active,
.grayscale img:hover,.grayscale img:focus,.grayscale img:active {
	opacity: 1;
    filter: brightness( 100% ) contrast( 100% ) saturate( 100% ) blur( 0px ) hue-rotate( 0deg );
}


 .editor-styles-wrapper .wp-site-blocks > * {
    margin-top: 0!important;
    margin-bottom: 0!important;
}



// Default Blocks
// login logout

.wp-block-loginout {
	a::before {
		content: "\f007";
		display: inline-block;
		/* stylelint-disable-next-line */
		font-family: "Font Awesome 5 Free";
		margin-right: 10px;
	}
}

// Category/archive/Recentpost

.wp-block-categories,
.wp-block-archives {
	list-style: none;
	padding: 0;
	 li {
		 a {
			display: inline-block;
			padding: 5px 0;
			text-decoration: inherit;
			&::before {
				content: "";
				display: inline-block;
				/* stylelint-disable-next-line */
				font-family: "Font Awesome 5 Free";
				font-size: 13px;
				margin-right: 8px;
			}
		 }
	 }
}

.wp-block-categories li a::before {
	content: "\f017";
}

.wp-block-archives li a::before {
	content: "\f07b";
}

// For Editor style
.editor-styles-wrapper {
	.wp-block-categories ul,
	.wp-block-archives ul,
	.wp-block-page-list {
		list-style: none;
		padding: 0;
	}
}

.taxonomy-category.wp-block-post-terms a,
.taxonomy-post_tag.wp-block-post-terms  a,
.wp-block-tag-cloud  a  {
	border: 2px solid;
	display: inline-block;
	font-size: 14px !important;
	margin: 5px;
	padding: 2px 10px;
	text-decoration: inherit !important;
}

span.wp-block-post-terms__separator {
    display: none;
}
.wp-block-latest-comments__comment-meta {
	.wp-block-latest-comments__comment-author,
	.wp-block-latest-comments__comment-link,
	.wp-block-latest-comments__comment-date{
		&::before {
			content: "";
			display: inline-block;
			/* stylelint-disable-next-line */
			font-family: "Font Awesome 5 Free";
			font-size: 13px;
			margin-right: 10px;
		}
	}

	.wp-block-latest-comments__comment-date::before {
		content: "\f017";
	}

}

// latest post

.wp-block-latest-posts li {
    clear: both;
	margin-bottom: 15px;
	overflow: hidden;
	.wp-block-latest-posts__featured-image {
		vertical-align: middle;
	}
}
.wp-block-latest-posts {
	.wp-block-latest-posts__post-title {
		font-weight: 600;
		display: block;
		padding-top: 10px;
	}
	li > a {
		font-weight: 600;
		display: block;
		padding-top: 10px;
	}
}

// post auther and date

.wp-block-latest-posts__post-author,
.wp-block-latest-posts__post-date,
.wp-block-post-date {
	display: inline-block;
	margin-right: 10px;
	&::before {
		content: "";
		display: inline-block;
		/* stylelint-disable-next-line */
		font-family: "Font Awesome 5 Free";
		font-size: 13px;
		margin-right: 10px;
	}
}
.wp-block-latest-posts__post-author::before {
	content: "\f007";
}

.wp-block-latest-posts__post-date:before,.wp-block-post-date:before {
	content: "\f017";
}

.widget a:not(.wp-block-button__link),
.entry-summery a:not(.wp-block-button__link),
.entry-content.wp-block-post-content a:not(.wp-block-button__link) {
	text-decoration: inherit;
}

.wp-block-latest-comments {
	padding: 0;
}

a.wp-block-post-excerpt__more-link {
	font-weight: 600;
    margin-top: 5px;
	display: block;
	&::after {
		content: "\f105";
		display: inline-block;
		/* stylelint-disable-next-line */
		font-family: "Font Awesome 5 Free";
		font-size: 13px;
		margin-left: 10px;
	}
}
a.wp-block-post-excerpt__more-link + a.wp-block-post-excerpt__more-link::after {
    display: none;
}

//Default Search form

.wp-site-blocks,
.editor-styles-wrapper {
	.wp-block-search{
		.wp-block-search__inside-wrapper {
			padding:0;
			.wp-block-search__button {
				cursor: pointer;
			}
			.wp-block-search__button,
			.wp-block-search__input {
				border: 1px solid transparent;
				padding: 9px 25px;
			}
			.wp-block-search__input {
				background: #f1f1f1;
				padding: 9px 0 9px 25px;
			}
		}
		&.transparent-field {
			.wp-block-search__inside-wrapper {
				.wp-block-search__input {
					background:transparent;
				}
			}
		}
		&.field-light-color {
			.wp-block-search__inside-wrapper {
				.wp-block-search__input {
					color:#fff;
				}
			}
		}
		&.wp-block-search__icon-button {
			.wp-block-search__button {
				padding:9px 12px;
			}
		}
	}
}

.clear-fix:after,
.clear-fix:before{
	content: "";
	display: table;
	table-layout: fixed;
	clear:both;
}

// Post auther
.wp-block-post-author {
	align-items: center;
}
.wp-block-post-author__avatar {
	img {
		border-radius: 50%;
		vertical-align: middle;
	}
}



// paginations
.wp-block-query-pagination {
	margin: 80px 0;
	.wp-block-query-pagination-numbers {
		margin: 0;
		display: inline-block;
	}
	.page-numbers,
	.wp-block-query-pagination-next,
	.wp-block-query-pagination-previous {
		padding: 12px 16px;
		background: #ffffff;
		line-height: 1;
		margin: 0 3px;
		text-decoration: inherit;
		display: inline-block;
		box-shadow: 0 0 15px #ddd;
		border-radius: 100px;
	}
	.page-numbers.current,
	a:hover,
	a:focus,
	a:active {
		background-color: var(--wp--preset--color--primary);
		color: #fff;
	}
}
