@import "./mixin";

[data-type="gutenify/buttons"] {
	&[data-align="wide"] {
		clear: both;
		margin-left: auto;
		margin-right: auto;
		max-width: 1100px;
	}
	&[data-align="full"] {
		clear: both;
		margin-left: -8px;
		margin-right: -8px;
		max-width: none;
	}
	.wp-block-gutenify-buttons {
		// column-gap: 0.5em;
		// display: flex;
		// flex-direction: row;
		// flex-wrap: wrap;
		// width: 100%;
		& > .wp-block {
			margin-top: 0.5em;
		}
		.block-editor-inner-blocks {
			& > div {
				column-gap: 0.5em;
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				align-items: center;
				justify-content: flex-start;
			}
		}
		&.is-content-justification-center {
			.block-editor-inner-blocks {
				& > div {
					justify-content: center;
				}
			}
			.wp-block-gutenify-button {
				margin: 0 3px;
			}
		}
		&.is-content-justification-left {
			.block-editor-inner-blocks {
				& > div {
					justify-content: flex-start;
				}
			}
			.wp-block-gutenify-button {
				margin: 0 6px 0 0;
			}
		}
		&.is-content-justification-right {
			.block-editor-inner-blocks {
				& > div {
					justify-content: flex-end;
				}
			}
			.wp-block-gutenify-button {
				margin: 0 0 0 6px;
			}
		}
		&.is-content-justification-space-between {
			display: block;
			.gutenify-button-section {
				column-gap: 0.5em;
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				justify-content: space-between;
			}
			.editor-styles-wrapper & {
				& > div {
					& > div {
						justify-content: space-between;
					}
				}
			}
		}
		&.is-vertical {
			flex-direction: column;
			.editor-styles-wrapper & {
				& > div {
					& > div {
						flex-direction: column;
					}
				}
			}

			&.is-content-justification-left {
				align-items: flex-start;
			}
			&.is-content-justification-right {
				align-items: flex-end;
			}
			&.is-content-justification-center {
				align-items: center;
			}
		}
	}
}
