@mixin gutenify-post-list-content-space-style {
	padding: 30px 40px;
}
@mixin gutenify-post-list-item-style {
	height: auto;
}
@mixin gutenify-post-list-meta-item-style {
	content: "";
	display: inline-block;
	/* stylelint-disable-next-line */
	font-family: "Font Awesome 5 Free";
	font-size: 13px;
	font-weight: 900;
	margin-right: 10px;
}
@mixin gutenify-post-list-item-box-shadow-style {
	border: none;
	box-shadow: 0 0 32px 0 #7a7a7a1a;
}
@mixin gutenify-post-list-meta-comments-link-icon-style {
	content: "\f086";
}
@mixin gutenify-post-list-meta-reviews-icon-style {
	content: "\f005";
}
@mixin gutenify-post-list-meta-cat-links-icon-style {
	content: "\f07c";
}
@mixin gutenify-post-list-meta-byline-icon-style {
	content: "\f406";
}
@mixin gutenify-post-list-meta-tags-links-icon-style {
	content: "\f02c";
	font-size: 10px;
	font-weight: 900;
}
@mixin gutenify-post-list-meta-posted-on-link-icon-style {
	content: "\f017";
}
@mixin gutenify-post-list-more-link-style {
	box-sizing: border-box;
	clear: both;
	display: block;
	display: table;
	font-weight: 600;
	margin-top: 15px;
	position: relative;
}
@mixin gutenify-post-list-more-link-icon-style {
	border-radius: 100%;
	content: "\f061";
	/* stylelint-disable-next-line */
	font-family: "Font Awesome 5 Free";
	font-size: 10px;
	font-weight: 900;
	padding: 7px;
	padding-left: 5px;
	right: 8px;
	text-align: center;
	top: 0;
}

// column style

@mixin gutenify-post-list-block-wrapper-style {
	display: grid;
	grid-gap: 30px;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	max-width: 100%;
}
@mixin post-list-category-item  {
	background: var(--gutenify--post-list--category-background);
	color: #222;
	display: inline-block;
	font-size: 13px;
	margin: 5px;
	padding: 0 10px;
}


/*
* Media
*/

@mixin wp-block-gutenify-post-list-media {
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
