@import "./mixin";

[data-type="gutenify/testimonials"] {
	.wp-block-gutenify-testimonials {
		& > .gutenify-testimonials-section {
			& > .gutenify-testimonials-block {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						@include testimonial-block-wrapper-style;
					}
				}
			}
		}
	}
	.wp-block-gutenify-testimonials.gutenify-testimonials-col-1 {
		& > .gutenify-testimonials-section {
			& > .gutenify-testimonials-block {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						grid-template-columns: 1fr;
					}
				}
			}
		}
	}
	.wp-block-gutenify-testimonials.gutenify-testimonials-col-2 {
		& > .gutenify-testimonials-section {
			& > .gutenify-testimonials-block {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						grid-template-columns: repeat(2, 1fr);
					}
				}
			}
		}
	}
	.wp-block-gutenify-testimonials.gutenify-testimonials-col-3 {
		& > .gutenify-testimonials-section {
			& > .gutenify-testimonials-block {
