			& > .gutenify-testimonials-block {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						grid-template-columns: repeat(7, 1fr);
					}
				}
			}
		}
	}
	.wp-block-gutenify-testimonials.gutenify-testimonials-col-8 {
