@import "./mixin";

.wp-block-gutenify-section-title {
	h1 {
		@include gutenify-section-h1-style;
	}
	h2 {
		@include gutenify-section-h2-style;
	}
	h3 {
		@include gutenify-section-h3-style;
	}
	h4 {
		@include gutenify-section-h4-style;
	}
	h5 {
		@include gutenify-section-h5-style;
	}
	h6 {
		@include gutenify-section-h6-style;
	}

	p {
		@include gutenify-section-p-style;
	}
}

@media screen and (max-width: 1023px) {
	.wp-block-gutenify-container,.wp-block-group {
		.wp-block-gutenify-section-title {
			h1 {
				@include gutenify-section-h1-tablet-style;
				br {
					display: none;
				}
			}
			h2 {
				@include gutenify-section-h2-tablet-style;
				br {
					display: none;
				}
			}
			h3 {
				@include gutenify-section-h3-tablet-style;
				br {
					display: none;
				}
			}
			h4 {
				@include gutenify-section-h4-tablet-style;
				br {
					display: none;
				}
			}
			h5 {
				@include gutenify-section-h5-tablet-style;
				br {
					display: none;
				}
			}
			h6 {
				@include gutenify-section-h6-tablet-style;
				br {
					display: none;
				}
			}

			p {
				@include gutenify-section-p-tablet-style;
				br {
					display: none;
				}
			}
		}
	}
}
@media screen and (max-width: 767px) {
	.wp-block-gutenify-container,.wp-block-group {
		.wp-block-gutenify-section-title {
			h1 {
				@include gutenify-section-h1-mobile-style;
				br {
					display: none;
				}
			}
			h2 {
				@include gutenify-section-h2-mobile-style;
				br {
					display: none;
				}
			}
			h3 {
				@include gutenify-section-h3-mobile-style;
				br {
					display: none;
				}
			}
			h4 {
				@include gutenify-section-h4-mobile-style;
				br {
					display: none;
				}
			}
			h5 {
				@include gutenify-section-h5-mobile-style;
				br {
					display: none;
				}
			}
			h6 {
				@include gutenify-section-h6-mobile-style;
				br {
					display: none;
				}
			}
			p {
				@include gutenify-section-p-mobile-style;
				br {
					display: none;
				}

			}
		}
	}
}
