@import "./mixin";
@import "./dynamic";

.wp-block-gutenify-wc-product-carousel{
	.gutenify--wc-product--item  {
		box-shadow: 0 0 32px 0 #7a7a7a1a;
		.gutenify--wc-product--item-wrapper{
			.gutenify--wc-product--item-content {
				padding: 30px;
			}
			position: relative;
			text-align: center;
			.gutenify--wc-product--thumb {
				overflow: hidden;
				position: relative;
				img{
					background: #f1f1f1;
					vertical-align: middle;
				}
			}
			.gutenify--wc-product--price {
				margin-bottom: 15px;
				font-weight: 500;
			}
			.wp-block-button.wc-block-grid__product-add-to-cart a {
				padding: 4px 20px;
				display: inline-block;
				margin: 15px 0 0;
			}
			.gutenify--wc-product--title {
				font-size: 1.2rem;
				font-weight: 500;
			}
			.gutenify--wc-product--onsale {
				background: #ffa500;
				border: none;
				border-radius: 0;
				color: #fff;
				font-size: 14px;
				padding: 3px 12px;
				position: absolute;
				right: 0;
				top: 12px;
				transform: rotate(89deg);
				z-index: 9;
				margin: 0;
				line-height: inherit;
				min-width: auto;
				min-height: auto;
			}
			del {
				opacity: 0.8;
			}
			mark,
			ins {
				background: none;
				font-weight:600;
			}
		}
	}
	&.gutenify--wc-product-list-layout-2 {
		.gutenify--wc-product--item-wrapper {
			.wp-block-button.wc-block-grid__product-add-to-cart {
				position: absolute;
				top: 0;
				/* transform: inherit; */
				display: flex;
				justify-content: space-around;
				width: 100%;
				height: 60%;
				align-items: center;
				align-content: stretch;
				flex-direction: row;
				visibility: hidden;
				transition: 0.3s;
				left: 0;
			}
		}
		.gutenify--wc-product--item-wrapper:hover {
			.wp-block-button.wc-block-grid__product-add-to-cart {
				visibility: visible;
				height: 70%;
				top: 0;
				transition: 0.3s;
			}
		}
	}
	&.gutenify--wc-product-list-layout-3 {
		.gutenify--wc-product--item  {
			.gutenify--wc-product--thumb {
				grid-area: wc-product-thumb;
			}
			.gutenify--wc-product--item-content {
				grid-area: wc-product-content;
				text-align: left;
				display: grid;
				align-content: center;
			}
			.wp-block-button.wc-block-grid__product-add-to-cart a {
				padding: 2px 10px;
				display: inline-block;
				margin: 10px 0 0;
				font-size: 14px;
				font-weight: 500;
			}
			.gutenify--wc-product--item-wrapper {
				align-content: start;
				display: grid;
				grid-template-areas:
					"wc-product-thumb wc-product-content"
					"wc-product-thumb  wc-product-content";
				grid-template-columns: 1fr 1fr;
				grid-template-rows: auto;
			}
			.gutenify--wc-product--title{
				font-size: 1.2rem;
				margin-bottom: 5px;
			}
		}
	}
	.swiper-pagination {
		// @include custom-swiper-pagination-style;
		.swiper-pagination-bullet {
			@include custom-swiper-pagination-bullet-style;
		}
	}
	.swiper-button-next {
		right: -100px;
	}
	.swiper-button-prev {
		left: -100px;
	}

	&:hover .swiper-button-next {
		right: 10px;
		visibility: visible;
	}
	&:hover .swiper-button-prev {
		left: 10px;
		visibility: visible;
	}

	.swiper-button-next,
	.swiper-button-prev {
		@include swiper-next-prev-style;
	}
	.swiper-button-next:hover,
	.swiper-button-prev:hover {
		background-color: #000;
		color: #fff;
	}
	.swiper-button-next::after,
	.swiper-button-prev::after {
		font-size: 17px;
	}
}

