	.gutenify-post-carousel-item {
		@include gutenify-post-carousel-item-style;
		.gutenify-post-carousel-item-inner-wrapper {
			@include gutenify-post-carousel-item-box-shadow-style;
			.gutenify-post-carousel-thumb {
				grid-area: carousel-thumb;
				img {
					object-fit: cover;
					vertical-align: middle;
					width: 100%;
				}
			}
			.gutenify-post-carousel-text-content {
				grid-area: carousel-text-content;

				@include gutenify-post-carousel-content-space-style;
				a {
					text-decoration: inherit !important;
				}
				h3 {
					grid-area: gutenify-post-carousel-title;
					margin-top: 0;
				}

				.gutenify-post-carousel-meta {
					grid-area: carousel-meta;
					margin-bottom: 15px;
					font-size: 15px;

					> span {
						display: inline-block;
						margin-right: 15px;
					}

					span::before {
						@include gutenify-post-carousel-meta-item-style;
					}
					span.comments-link::before {
						@include gutenify-post-carousel-meta-comments-link-icon-style;
					}
					span.reviews::before {
						@include gutenify-post-carousel-meta-reviews-icon-style;
					}
					.cat-links::before {
						@include gutenify-post-carousel-meta-cat-links-icon-style;
					}
					.byline::before {
						@include gutenify-post-carousel-meta-byline-icon-style;
					}
					.tags-links::before {
						@include gutenify-post-carousel-meta-tags-links-icon-style;
					}
					.posted-on::before {
						@include gutenify-post-carousel-meta-posted-on-link-icon-style;
					}
				}
				.wp-block-gutenify-posts__more-link,
				.wp-block-gutenify-posts__more-link:visited {
					@include gutenify-post-carousel-more-link-style;
				}
				.wp-block-gutenify-posts__more-link::after {
					@include gutenify-post-carousel-more-link-icon-style;
				}
				.entry-summary {
					grid-area: carousel-entry-summary;
					margin: 0;
				}
			}
		}
	}
	&.gutenify-post-carousel-layout-1 {
		.gutenify-post-carousel-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-carousel-text-content {
				// padding: 40px;
				position: relative;
				span.cat-links {
					left: 25px;
					position: absolute;
					top: -50px;
					a {
					@include post-carousel-category-item;
					}
					span::before {
						display: none;
					}
					&::before {
						display: none;
					}
				}
			}
		}
	}
	&.gutenify-post-carousel-layout-2 {
		.gutenify-post-carousel-item {
			.gutenify-post-carousel-item-inner-wrapper {
				align-content: start;
				background-color: #fff;
				display: grid;
				grid-template-areas:
					"carousel-thumb carousel-text-content"
					"carousel-thumb carousel-text-content";
				grid-template-columns: 1fr 1fr;
				grid-template-rows: auto;

				.gutenify-post-carousel-text-content {
					align-content: center;
					display: grid;
					grid-template-areas:
						"gutenify-post-carousel-title"
						"carousel-meta"
						"carousel-entry-summary"
						"carousel-thumb";
					padding: 40px;
				}
			}
		}
	}
	&.gutenify-post-carousel-layout-3 {
		.gutenify-post-carousel-item {
			img {
				min-height: 500px;
				object-fit: cover;
				vertical-align: middle;
				width: 100%;
			}
			.gutenify-post-carousel-item-inner-wrapper {
				position: relative;
				a {
					color: #fff;
				}
				.gutenify-post-carousel-thumb {
					position: relative;
				}
				.gutenify-post-carousel-text-content p {
					margin:0;
				}
				.gutenify-post-carousel-text-content {
					background: linear-gradient(to top,rgba(0,0,0,.9) 0,rgba(0,0,0,0) 100%);
					bottom: 0;
					color: #fff;
					padding: 0 10% 10%;
					position: absolute;
					max-width: 100%;
					display: flex;
					align-content: flex-end;
					flex-wrap: wrap;
					gap: 0;
					height: 93%;
					h3 {
						width: 100%;
					}
					.entry-summary {
						width: 100%;
					}
				}

			}
		}
	}
	&.gutenify-post-carousel-layout-4 {
		.gutenify-post-carousel-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-carousel-text-content {
				position: relative;
				align-content: start;
				display: grid;
				grid-template-areas:
					"gutenify-post-carousel-title"
					"carousel-entry-summary"
					"carousel-meta";
				padding: 0;
				grid-template-columns: 100%;
				grid-template-rows: auto;
				.gutenify-post-carousel-meta {
					margin: 0;
					padding: 10px 40px;
					border-top: 1px solid #0000000d;
					display: flex;
					justify-content: space-between;
				}
				.gutenify-post-carousel-title {
					margin: 0;
					padding: 25px 40px 0;
				}
				.entry-summary {
					margin: 0;
					padding: 0 40px 20px;
				}

				span.cat-links {
					left: 6%;
					position: absolute;
					top: -20%;
					a {
					@include post-carousel-category-item;
					}
				}
				span.cat-links::before {
					display: none;
				}
			}
		}
	}
	&.gutenify-post-carousel-layout-5 {
		.gutenify-post-carousel-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-carousel-text-content {
				position: relative;
				align-content: start;
				display: grid;
				grid-template-areas:
					"carousel-meta"
					"gutenify-post-carousel-title"
					"carousel-entry-summary";
				padding: 0;
				grid-template-columns: 100%;
				grid-template-rows: auto;
				.gutenify-post-carousel-meta {
					margin: 0;
					padding: 10px 40px;
					border-bottom: 1px solid #0000000d;
					display: flex;
					justify-content: space-between;
				}
				.gutenify-post-carousel-title {
					margin: 0;
					padding: 22px 40px 0;
				}
				.entry-summary {
					margin: 0;
					padding: 0 40px 30px;

				}

				span.cat-links {
					left: 6%;
					position: absolute;
					top: -20%;
					a {
					     background: #ffe66a;
						color: #000;
						display: inline-block;
						font-size: 13px;
						margin: 5px;
						padding: 0 10px;
					}
				}
				span.cat-links::before {
					display: none;
				}
			}
		}
	}
	div.swiper-pagination {
		@include custom-swiper-pagination-style;
		.swiper-pagination-bullet {
			@include custom-swiper-pagination-bullet-style;
		}
	}
	.swiper-button-next {
		right: -100px;
	}
	.swiper-button-prev {
		left: -100px;
	}

	&:hover .swiper-button-next {
		right: 10px;
		visibility: visible;
	}
	&:hover .swiper-button-prev {
		left: 10px;
		visibility: visible;
	}

	.swiper-button-next,
	.swiper-button-prev {
		@include swiper-next-prev-style;
	}
	.swiper-button-next:hover,
	.swiper-button-prev:hover {
		background-color: #000;
		color: #fff;
	}
	.swiper-button-next::after,
	.swiper-button-prev::after {
		font-size: 17px;
	}
}
@media screen and (max-width: 768px) {
	.wp-block-gutenify-post-carousel.gutenify-post-carousel-layout-2 {
		.gutenify-post-carousel-item {
			.gutenify-post-carousel-item-inner-wrapper {
				align-content: start;
				display: grid;
				grid-template-areas:
					"carousel-thumb"
					"carousel-text-content";
				grid-template-columns: 1fr;
				grid-template-rows: auto;
			}
		}

	}
	&.gutenify-post-carousel-layout-3 {
		.gutenify-post-carousel-section {
			.gutenify-post-carousel-item {
				.gutenify-post-carousel-item-inner-wrapper {
					.gutenify-post-carousel-thumb img {
					min-height: 400px;
					}
				}
			}

		}
	}
}
