@import "./mixin";

[data-type="gutenify/button"] {
	display: inline-block;
	margin-bottom: 0;
	margin-top: 0;

	.has-custom-font-size {
		.gutenify-button-link {
			font-size: inherit;
		}
	}
}

.gutenify-button-link.wp-block-button__link {
	@include gutenify-button-link-style;
}

.editor-styles-wrapper {
	@import "./dynamic";
}
