
// column style

@mixin gutenify--wc-product-list-block-wrapper-style {
	display: grid;
	grid-gap: 30px;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	max-width: 100%;
}


/*
* Media
*/

@mixin wp-block-gutenify--wc-product-list-media {
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
