@import "./mixin";
@import "./dynamic";

.wp-block-gutenify-post-list {
	// column style
	margin: 15px 0;
	@include gutenify-post-list-block-wrapper-style;

	&.gutenify-post-list-col-1 {
			grid-template-columns: 1fr;
		}


	&.gutenify-post-list-col-2 {
			grid-template-columns: repeat(2, 1fr);
		}


	&.gutenify-post-list-col-3 {
			grid-template-columns: repeat(3, 1fr);
		}


	&.gutenify-post-list-col-4 {
			grid-template-columns: repeat(4, 1fr);
		}


	&.gutenify-post-list-col-5 {
			grid-template-columns: repeat(5, 1fr);
		}

	&.gutenify-post-list-col-6 {
			grid-template-columns: repeat(6, 1fr);

	}
	&.gutenify-post-list-col-7 {
		grid-template-columns: repeat(7, 1fr);

	}
	&.gutenify-post-list-col-8 {
		grid-template-columns: repeat(8, 1fr);

	}

	.gutenify-post-list-item {
		@include gutenify-post-list-item-style;
		.gutenify-post-list-item-inner-wrapper {
			@include gutenify-post-list-item-box-shadow-style;
			.gutenify-post-list-thumb {
				grid-area: post-list-thumb;
				img {
					object-fit: cover;
					vertical-align: middle;
					width: 100%;
				}
			}

		}
		.gutenify-post-list-text-content {
			grid-area: post-list-text-content;

			@include gutenify-post-list-content-space-style;
			a {
				text-decoration: inherit;
			}
			h3 {
				grid-area: gutenify-post-list-title;
				margin-top: 0;
			}
			.gutenify-post-list-meta {
				grid-area: post-list-meta;
				margin-bottom: 15px;

				> span {
					display: inline-block;
					margin-right: 15px;
					font-size: 15px;
				}

				span::before {
					@include gutenify-post-list-meta-item-style;
				}
				span.comments-link::before {
					@include gutenify-post-list-meta-comments-link-icon-style;
				}
				span.reviews::before {
					@include gutenify-post-list-meta-reviews-icon-style;
				}
				.cat-links::before {
					@include gutenify-post-list-meta-cat-links-icon-style;
				}
				.byline::before {
					@include gutenify-post-list-meta-byline-icon-style;
				}
				.tags-links::before {
					@include gutenify-post-list-meta-tags-links-icon-style;
				}
				.posted-on::before {
					@include gutenify-post-list-meta-posted-on-link-icon-style;
				}
			}
			.wp-block-gutenify-posts__more-link,
			.wp-block-gutenify-posts__more-link:visited {
				@include gutenify-post-list-more-link-style;
			}
			.wp-block-gutenify-posts__more-link::after {
				@include gutenify-post-list-more-link-icon-style;
			}
			.entry-summary {
				grid-area: post-list-entry-summary;
				margin: 0;
			}
		}
	}
	&.gutenify-post-list-layout-1 {
		.gutenify-post-list-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-list-text-content {
				position: relative;
				span.cat-links {
					left: 6%;
					position: absolute;
					top: -20%;
					a {
					@include post-list-category-item;
					}
				}
				span.cat-links::before {
					display: none;
				}
			}
		}
	}

	&.gutenify-post-list-layout-2 {
		.gutenify-post-list-item {
			.gutenify-post-list-item-inner-wrapper {
				align-content: start;
				display: grid;
				grid-template-areas:
					"post-list-thumb post-list-text-content"
					"post-list-thumb post-list-text-content";
				grid-template-columns: 1fr 2fr;
				grid-template-rows: auto;
				img {
					vertical-align: middle;
					width: 100%;
					min-height: 100%;
					-o-object-fit: cover;
					object-fit: cover;
					width: 100%;
					max-height: 100%;
				}
			}
		}
		.gutenify-post-list-text-content {
			align-content: center;
			display: grid;
			grid-template-areas:
				"gutenify-post-list-title"
				"post-list-meta"
				"post-list-entry-summary"
				"post-list-thumb";
				padding:30px;
				p {
					margin-top:0;
				}
		}
	}
	&.gutenify-post-list-layout-3 {
		.gutenify-post-list-item {
			img {
				min-height: 500px;
				object-fit: cover;
				vertical-align: middle;
				width: 100%;
			}
			.gutenify-post-list-item-inner-wrapper {
				position: relative;
				a {
					color: #fff;
					&:hover {
						text-decoration: underline;
					}
				}
				.gutenify-post-list-thumb {

					position: relative;
				}
				.gutenify-post-list-text-content  p {
					margin:0;
				}
				.gutenify-post-list-text-content {
					background: linear-gradient(to top,rgba(0,0,0,.9) 0,rgba(0,0,0,0) 100%);
					bottom: 0;
					color: #fff;
					padding: 0 10% 10%;
					position: absolute;
					max-width: 100%;
					display: flex;
					align-content: flex-end;
					flex-wrap: wrap;
					gap: 0;
					height: 93%;
					h3 {
						width: 100%;
					}
					.entry-summary {
						width: 100%;
					}
				}
			}
		}
	}
	&.gutenify-post-list-layout-4 {
		.gutenify-post-list-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-list-text-content {
				position: relative;
				align-content: start;
				display: grid;
				grid-template-areas:
					"gutenify-post-list-title"
					"post-list-entry-summary"
					"post-list-meta";
				padding: 0;
				grid-template-columns: 100%;
				grid-template-rows: auto;
				.gutenify-post-list-meta {
					margin: 0;
					padding: 10px 40px;
					border-top: 1px solid #0000000d;
					display: flex;
					justify-content: space-between;
				}
				.gutenify-post-list-title {
					margin: 0;
					padding: 25px 40px 0;
				}
				.entry-summary {
					margin: 0;
					padding: 0 40px 20px;
				}

				span.cat-links {
					left: 6%;
					position: absolute;
					top: -20%;
					a {
					@include post-list-category-item;
					}
				}
				span.cat-links::before {
					display: none;
				}
			}
		}
	}
	&.gutenify-post-list-layout-5 {
		.gutenify-post-list-item-inner-wrapper {
			background-color: #fff;
			.gutenify-post-list-text-content {
				position: relative;
				align-content: start;
				display: grid;
				grid-template-areas:
					"post-list-meta"
					"gutenify-post-list-title"
					"post-list-entry-summary";
				padding: 0;
				grid-template-columns: 100%;
				grid-template-rows: auto;
				.gutenify-post-list-meta {
					margin: 0;
					padding: 10px 40px;
					border-bottom: 1px solid #0000000d;
					display: flex;
					justify-content: space-between;
				}
				.gutenify-post-list-title {
					margin: 0;
					padding: 22px 40px 0;
				}
				.entry-summary {
					margin: 0;
					padding: 0 40px 30px;

				}

				span.cat-links {
					left: 6%;
					position: absolute;
					top: -20%;
					a {
					@include post-list-category-item;
					}
				}
				span.cat-links::before {
					display: none;
				}
			}
		}
	}
	&.gutenify--post-list--post-list-layout-2{
		.gutenify-post-list-item:first-child {
			.gutenify-post-list-text-content {
				padding: 40px;
				h3 {
					font-size: 2rem;
				}
			}
		}
	}

	&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-2 {
        .gutenify-post-list-item:first-child {
            grid-column: span 2;
        }

}

&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-3 {
        .gutenify-post-list-item:first-child {
            grid-column: span 3;
            .gutenify-post-list-text-content h3 {
                font-size: 2.2rem;
            }
        }

}

&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-4 {
        .gutenify-post-list-item:first-child {
            grid-column: span 4;
        }

}
&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-5 {
        .gutenify-post-list-item:first-child {
            grid-column: span 5;
        }

}

&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-6 {
        .gutenify-post-list-item:first-child {
            grid-column: span 6;
        }

}

}

@media screen and (max-width: 1023px) {
	.wp-site-blocks .wp-block-gutenify-post-list {
		@include wp-block-gutenify-post-list-media;
		&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-2,
		&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-3,
		&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-4,
		&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-5,
		&.gutenify--post-list--post-list-layout-2.gutenify-post-list-col-6 {
			.gutenify-post-list-item:first-child {
				grid-column: inherit;
				.gutenify-post-list-text-content {
					padding: 30px;
					 h3 {
						font-size: 1.3rem;
					}
				}
			}
		}
	}
}

@media screen and (max-width: 768px) {
	.wp-block-gutenify-post-list.gutenify-post-list-layout-2 {
		.gutenify-post-list-item {
			.gutenify-post-list-item-inner-wrapper {
				align-content: start;
				display: grid;
				grid-template-areas:
					"post-list-thumb"
					"post-list-text-content";
				grid-template-columns: 1fr;
				grid-template-rows: auto;
			}
		}
	}
}

