@mixin slider-content-wrapper-style {
	display: block;
	height: auto;
	margin: 0 auto;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
	width: 100%;
	z-index: 99;
}
@mixin gutenify-slider-content-inner {
	margin: 0 auto;
	max-width: 80%;
	width: 1180px;
}
@mixin gutenify-slider-inner-wrapper-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"slider-subtitle"
		"slider-title"
		"slider-content";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}
@mixin gallery-carousel-item {
	height: auto;
}
@mixin gutenify-slider-image-wrapper {
	height: 100%;
}
@mixin gutenify-slider-content-image {
	height: 100%;
}
@mixin gutenify-slider-content-image-img {
	height: 100%;
	object-fit: cover;
	width: 100%;
}
@mixin slider-overlay-style {
	background: #000;
	content: "";
	display: block;
	height: 100%;
	opacity: 0.6;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 9;
}
@mixin slider-text-color-style {
	color: #fff;
}
@mixin slider-text-style {
	font-size: 17px;
	margin-bottom: 20px;
}
@mixin slider-title-text-style {
	font-size: 55px;
	line-height: 1.3;
}
@mixin slider-button-style {
	color: inherit;
	text-decoration: none;
}
@mixin swiper-next-prev-style {
	background: #fff;
	color: #fff;
	color: #000;
	cursor: pointer;
	transition: all 0.2s ease-in-out 0s;
	width: 45px;
	height: 45px;
	z-index: 99;
}
@mixin swiper-next-style {
	right: -150px;
}
@mixin swiper-prev-style {
	left:-150px;
}
@mixin swiper-next-prev-icon-style {
	font-size: 20px;
}
@mixin custom-swiper-pagination-style {
	bottom: 30px;
	left: 0;
	position: absolute;
	width: 100%;
	z-index: 99;
}
@mixin custom-swiper-pagination-bullet-style {
	background: #fff;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: unset;
	height: 15px;
	line-height: unset;
	margin: 0 10px;
	padding: unset;
	position: relative;
	-webkit-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
	width: 15px;
}

/*
* Media Tablet
*/
@mixin slider-text-tablet-style {
	font-size: 14px;
}
@mixin slider-title-text-tablet-style {
	font-size: 40px;
}


/*
* Media mobile
*/

@mixin slider-text-mobile-style {
	-webkit-box-orient: vertical;
	display: -webkit-box;
	font-size: 14px;
	-webkit-line-clamp: 1;
	margin-bottom: 10px;
	overflow: hidden;
	text-overflow: ellipsis;
}
@mixin slider-title-text-mobile-style {
	font-size: 25px;
	line-height: 1.2;
	margin-bottom: 5px;
}
@mixin custom-swiper-pagination-mobile-style {
	bottom: 0;
}
@mixin custom-swiper-pagination-bullet-mobile-style {
	margin: 0 10px;
}
