

@mixin swiper-next-prev-style {
	background: #fff;
	color: #fff;
	color: #000;
	cursor: pointer;
	transition: all 0.2s ease-in-out 0s;
	width: 45px;
	z-index: 99;
}
@mixin swiper-next-style {
	right: 0;
}
@mixin swiper-prev-style {
	left: 0;
}
@mixin swiper-next-prev-icon-style {
	font-size: 20px;
}
@mixin custom-swiper-pagination-style {
	bottom: 30px;
	left: 0;
	position: absolute;
	width: 100%;
	z-index: 99;
}
@mixin custom-swiper-pagination-bullet-style {
	background: #fff;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: unset;
	height: 15px;
	line-height: unset;
	margin: 0 10px;
	padding: unset;
	position: relative;
	-webkit-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
	width: 15px;
}

/*
* Media Tablet
*/
@mixin slider-text-tablet-style {
	font-size: 14px;
}
@mixin slider-title-text-tablet-style {
	font-size: 40px;
}
@mixin slider-button-tablet-style {
	font-size: 14px;
	padding: 5px 20px;
}
@mixin swiper-next-prev-tablet-style {
	padding: 15px;
}
@mixin swiper-next-prev-after-tablet-style {
	font-size: 14px;
}

/*
* Media mobile
*/

@mixin slider-text-mobile-style {
	-webkit-box-orient: vertical;
	display: -webkit-box;
	font-size: 14px;
	-webkit-line-clamp: 1;
	margin-bottom: 10px;
	overflow: hidden;
	text-overflow: ellipsis;
}
@mixin slider-title-text-mobile-style {
	font-size: 25px;
	line-height: 1.2;
	margin-bottom: 5px;
}
@mixin slider-button-mobile-style {
	font-size: 13px;
	line-height: 1.5;
	padding: 3px 11px;
}
@mixin swiper-next-prev-mobile-style {
	padding: 12px;
	width: 35px;
}
@mixin swiper-next-mobile-style {
	right: 0;
}
@mixin swiper-prev-mobile-style {
	left: 0;
}
@mixin swiper-next-prev-after-mobile-style {
	font-size: 14px;
}
@mixin custom-swiper-pagination-mobile-style {
	bottom: 15px;
}
@mixin custom-swiper-pagination-bullet-mobile-style {
	height: 10px;
	margin: 0 3px;
	width: 10px;
}
