@import "./mixin";

[data-type="gutenify/icon"] {
	.gutenify-icon-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		overflow: hidden;

		img {
			height: 100%;
			object-fit: contain;
		}
	}
	.has-shadow-dark {
		box-shadow: unset;
		&:hover {
			box-shadow: unset;
			transform: unset;
		}
		> * {
			box-shadow: 2px 2px 30px 0 #0000001a;
		}
	}
}
