@mixin gutenify-info-box-item-block-wrapper-style {
	text-align: center;
}
@mixin gutenify-info-box-block-grid-wrapper-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"info-box-icon"
		"info-box-title"
		"info-box-subtitle"
		"info-box-content"
		"info-box-button";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}
@mixin gutenify-info-box-block-grid-wrapper-layout-two-style {
	column-gap: 15px;
	grid-template-areas:
		"info-box-icon info-box-title"
		"info-box-icon info-box-subtitle"
		"info-box-icon info-box-content"
		"info-box-icon info-box-button";
	grid-template-columns: auto 1fr;
}
@mixin gutenify-info-box-block-grid-wrapper-layout-three-style {
	column-gap: 15px;
	grid-template-areas:
		"info-box-title info-box-icon "
		"info-box-subtitle info-box-icon"
		"info-box-content info-box-icon"
		"info-box-button info-box-icon";
	grid-template-columns: 1fr auto;
}
