@import "./mixin";

[data-type="gutenify/grid"] {
	--gutenify--grid-template-columns: 3;
	> .block-editor-inner-blocks {
		> .block-editor-block-list__layout {
			display: grid;
			grid-template-columns: repeat(
				var(--gutenify--grid-template-columns),
				1fr
			);
			gap: 40px;
			justify-content: space-between;
		}
	}
}

@media screen and (max-width: 1023px) {
	[data-type="gutenify/grid"] > .block-editor-inner-blocks > .block-editor-block-list__layout{
		grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	}
}
