
// Mixin Style

@mixin swiper-next-prev-style {
	background: #fff;
	box-shadow: 0 0 12px #0000002b;
	color: #000;
	cursor: pointer;
	height: auto;
	padding: 15px;
	transition: all 0.2s ease-in-out 0s;
	visibility: hidden;
	width: auto;
	z-index: 99;
}
@mixin custom-swiper-pagination-style {
	bottom: inherit;
    left: 0;
    position: inherit;
    width: 100%;
    z-index: 99;
    margin-top: 15px;
}
@mixin custom-swiper-pagination-bullet-style {
	background: #000;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: unset;
	height: 15px;
	line-height: unset;
	margin: 0 5px;
	padding: unset;
	position: relative;
	-webkit-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
	width: 15px;
}
