@import "./mixin";
@import "../../../../node_modules/swiper/swiper.scss";
@import "../../../../node_modules/swiper/components/navigation/navigation.scss";
@import "../../../../node_modules/swiper/components/pagination/pagination.scss";

.editor-styles-wrapper .wp-block figure {
	margin: 0;
	padding: 0;
	img {
		width: 100%;
		vertical-align: middle;
	}
}
.gutenify-gallery-carousel-section {
	.gutenify-gallery-carousel-item {
		@include gallery-carousel-item;
		.gutenify-slider-image-wrapper {
			@include gutenify-slider-image-wrapper;
			.gutenify-slider-content-image {
				@include gutenify-slider-content-image;
				img {
					@include gutenify-slider-content-image-img;
				}
			}
		}
		.gutenify-slider-content-wrapper {
			@include slider-content-wrapper-style;
			.gutenify-slider-content-inner {
				@include gutenify-slider-inner-wrapper-style;
				@include gutenify-slider-content-inner;
			}
			.gutenify-slider-title {
				@include slider-title-text-style;
				@include slider-text-color-style;
				grid-area: slider-title;
			}
			.gutenify-slider-sub-title {
				@include slider-text-color-style;
				grid-area: slider-subtitle;
			}
			.gutenify-slider-content {
				grid-area: slider-content;
				p {
					@include slider-text-color-style;
					@include slider-text-style;
				}
			}
		}
	}
	.swiper-pagination {
		@include custom-swiper-pagination-style;
		.swiper-pagination-bullet {
			@include custom-swiper-pagination-bullet-style;
		}
	}
	.swiper-button-next,
	.swiper-button-prev {
		@include swiper-next-prev-style;
	}
	.swiper-button-next {
		@include swiper-next-style;
	}
	.swiper-button-prev {
		@include swiper-prev-style;
	}
	.swiper-button-next::after,
	.swiper-button-prev::after {
		@include swiper-next-prev-icon-style;
	}
	.gutenify-gallery-carousel-item::before {
		@include slider-overlay-style;
	}

	&:hover .swiper-button-prev {
		left:0;
	}
	&:hover .swiper-button-next {
		right:0;
	}

}
	// Columns style
.gutenify--gallery-carousel--col-2 .gutenify-gallery-carousel-section {
	.gutenify-gallery-carousel-item {
		.gutenify-slider-content-wrapper {
			.gutenify-slider-content-inner {
				.gutenify-slider-title {
					font-size: 2rem;
				}
				.gutenify-slider-sub-title {
					font-size: 1rem;
				}
			}

		}
	}
	.swiper-pagination {
		bottom: 0;
		left: 0;
		position: inherit;
		width: 100%;
		z-index: 99;
		margin-top: 15px;
	}
}
.gutenify--gallery-carousel--col-3 .gutenify-gallery-carousel-section,
.gutenify--gallery-carousel--col-4 .gutenify-gallery-carousel-section {
	.gutenify-gallery-carousel-item {
		.gutenify-slider-content-wrapper {
			.gutenify-slider-content-inner {
				.gutenify-slider-title {
					font-size: 1.5rem;
				}
				.gutenify-slider-sub-title {
					font-size: 1rem;
				}
			}

		}
	}
	.swiper-pagination {
		bottom: 0;
		width: 100%;
		z-index: 99;

	}

	.swiper-container {
		padding-bottom:50px;
	}
	.swiper-button-prev,
	.swiper-button-next {
		top:45%;
	}
}

.gutenify--gallery-carousel--layout-2 .gutenify-gallery-carousel-section{
	.gutenify-slider-content-inner {
		text-align: center;
	}

}
.gutenify--gallery-carousel--layout-3 .gutenify-gallery-carousel-section {

	&:hover .swiper-button-prev {
		left:5%;
	}
	&:hover .swiper-button-next {
		right:5%;
	}
	.gutenify-slider-content-inner {
		text-align: center;
	}
	.gutenify-gallery-carousel-item:before{
		display: none;
	}
	.gutenify-gallery-carousel-item {
		.gutenify-slider-content-wrapper {
			bottom: 0;
			top: inherit;
			transform: inherit;
			background: #000;
			padding: 20px;
			width: -webkit-fill-available;
			left: 0;
			p {
				margin-top: 0;
			}
		}
	}
	.gutenify--gallery-carousel--col-1 {
		.swiper-pagination {
			position: inherit;
			bottom: inherit;
			margin-top: 15px;
		}
		.gutenify-gallery-carousel-item .gutenify-slider-content-wrapper{
			padding: 40px 0;
		}
	}
}
@media screen and (max-width: 1023px) {
	.gutenify-gallery-carousel-section {
		.swiper-container {
			.gutenify-gallery-carousel-item {
				.gutenify-slider-title {
					@include slider-title-text-tablet-style;
				}
				.gutenify-slider-content p {
					@include slider-text-tablet-style;
				}

			}
		}
	}
}
@media screen and (max-width: 767px) {
	.gutenify--gallery-carousel--layout-3 .gutenify-gallery-carousel-item .gutenify-slider-content-wrapper {
		bottom: 0;
		top: inherit;
		transform: inherit;
	}
	.gutenify-gallery-carousel-section .gutenify-gallery-carousel-item .gutenify-slider-content-wrapper .gutenify-slider-sub-title {
		font-size: 1rem;
	}
	.gutenify-gallery-carousel-section {
		.swiper-container {
			.gutenify-gallery-carousel-item {
				.gutenify-slider-title {
					@include slider-title-text-mobile-style;
				}
				.gutenify-slider-content p {
					@include slider-text-mobile-style;
				}
			}
			.swiper-pagination {
				@include custom-swiper-pagination-mobile-style;
				.swiper-pagination-bullet {
					@include custom-swiper-pagination-bullet-mobile-style;
				}
			}

		}
	}
}
