:root {
	--gutenify--post-list--category-background: #ffe66a;
	--gutenify--post-list--category-color: #000;
}
@mixin gutenify-post-carousel-content-space-style {
	padding: 30px 40px;
}
@mixin gutenify-post-carousel-item-style {
	height: auto;
}
@mixin gutenify-post-carousel-meta-item-style {
	content: "";
	display: inline-block;
	/* stylelint-disable-next-line */
	font-family: "Font Awesome 5 Free";
	font-size: 13px;
	font-weight: 900;
	margin-right: 10px;
}
@mixin gutenify-post-carousel-item-box-shadow-style {
	background-color: #fff;
	box-shadow: 0 0 32px 0 #7a7a7a1a;
}
@mixin gutenify-post-carousel-meta-comments-link-icon-style {
	content: "\f086";
}
@mixin gutenify-post-carousel-meta-reviews-icon-style {
	content: "\f005";
}
@mixin gutenify-post-carousel-meta-cat-links-icon-style {
	content: "\f07c";
}
@mixin gutenify-post-carousel-meta-byline-icon-style {
	content: "\f406";
}
@mixin gutenify-post-carousel-meta-tags-links-icon-style {
	content: "\f02c";
	font-size: 10px;
	font-weight: 900;
}
@mixin gutenify-post-carousel-meta-posted-on-link-icon-style {
	content: "\f017";
}
@mixin gutenify-post-carousel-more-link-style {
	box-sizing: border-box;
	clear: both;
	display: block;
	display: table;
	font-weight: 600;
	margin-top: 15px;
	position: relative;
}
@mixin gutenify-post-carousel-more-link-icon-style {
	border-radius: 100%;
	content: "\f061";
	/* stylelint-disable-next-line */
	font-family: "Font Awesome 5 Free";
	font-size: 10px;
	font-weight: 900;
	padding: 7px;
	padding-left: 5px;
	right: 8px;
	text-align: center;
	top: 0;
}
@mixin slider-button-style {
	background: #255b3e;
	color: #fff;
	display: inline-block;
	font-size: 18px;
	font-weight: 500;
	margin-bottom: 30px;
	padding: 10px 25px;
}
@mixin swiper-next-prev-style {
	background: #fff;
	box-shadow: 0 0 12px #0000002b;
	color: #000;
	cursor: pointer;
	height: auto;
	padding: 15px;
	transition: all 0.2s ease-in-out 0s;
	visibility: hidden;
	width: auto;
	z-index: 99;
}
@mixin custom-swiper-pagination-style {
	bottom: 0;
	left: 0;
	/* margin-top: 30px; */
	position: absolute;
	width: 100%;
	z-index: 99;
}
@mixin custom-swiper-pagination-bullet-style {
	background: #000;
	border-radius: 50px;
	cursor: pointer;
	display: inline-block;
	font-size: unset;
	height: 15px;
	line-height: unset;
	margin: 0 5px;
	padding: unset;
	position: relative;
	-webkit-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
	width: 15px;
}

@mixin post-carousel-category-item  {
	background: var(--gutenify--post-list--category-background);
	color: #222;
	display: inline-block;
	font-size: 13px;
	margin: 5px;
	padding: 0 10px;
}
