@mixin team-member-content-wrapper {
	padding: 20px;
}
@mixin gutenify-team-member-block-grid-wrapper-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"team-member-thumb"
		"team-member-full-name"
		"team-member-degination"
		"team-member-bio"
		"team-member-social-links";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}
@mixin gutenify-team-member-block-grid-wrapper-layout-two-style {
	grid-template-areas:
		"team-member-thumb . team-member-full-name"
		"team-member-thumb . team-member-degination"
		"team-member-thumb . team-member-bio"
		"team-member-thumb . team-member-social-links";
	grid-template-columns: 48% 30px  auto;
	grid-template-rows: auto auto auto 1fr;
}
@mixin gutenify-team-member-block-grid-wrapper-layout-three-style {
	grid-template-areas:
		"team-member-full-name . team-member-thumb"
		"team-member-degination . team-member-thumb"
		"team-member-bio . team-member-thumb"
		"team-member-social-links . team-member-thumb";
	grid-template-columns: auto 30px 48%;
	grid-template-rows: auto auto auto 1fr;
}
