/**
 * Editor Styles
 */
// @import "../../variables";

.gutenify-toolbar-templates {
	flex: 100%;
	margin-left: 5px;
	svg {
		margin-bottom: -2px;
		margin-right: 6px;
		margin-top: -2px;
	}
}
//tab
.gutenify-control-tabs.gutenify-component-modal-tab-panel {
    margin-top: 20px;
    display: block;
    float: left;
    width: 100%;
}
.gutenify-control-tabs .gutenify-control-tabs-tab {
    background: #f1f1f1;
    margin: 5px;
    box-shadow: none;
    border: none;
    padding: 13px 20px;
    line-height: 1;
    height: auto;
}
.gutenify-control-tabs  .components-tab-panel__tabs-item:focus:not(:disabled) {
	box-shadow: none;
}
.gutenify-control-tabs .gutenify-control-tabs-tab.is-active,
.gutenify-control-tabs .gutenify-control-tabs-tab:hover {
    background: #4caf50;
    color: #fff;
}


.gutenify-plugin-templates-modal button.components-button.components-icon-button {
	background: #f55347;
    color: #fff;
    border-radius: 100%;
    height: auto;
    width: auto;
    text-align: center;
    padding: 4px;
}
.gutenify-plugin-templates-modal {
    width: 85%;
    max-height: 85%;
    min-height: 85%;
    min-width: 80%;
	.components-modal__header .components-button,
	.components-modal__header .components-button:hover,
	.components-modal__header .components-button:focus,
	.components-modal__header .components-button:active {
		position: relative;
		background: #4caf50;
		box-shadow: none;
		color: #fff;
		margin-left: 10px;
		padding: 6px;
		height: auto;
		cursor: pointer;
	}
	&.gutenify-plugin-templates-modal-hide-header
		.components-modal__content
		> .components-modal__header:first-of-type {
		display: none;

	}

	.components-modal__header {
		background: #f1f1f1;
		border-bottom: none;
		border-bottom: 1px solid #ddd;
		height: 50px;
	}

	.gutenify-component-modal-tab-panel .components-tab-panel__tabs {
		margin-top: -24px;
	}

	// loading while trying to insert selected template.
	&.gutenify-plugin-templates-modal-loading {
		.gutenify-control-tabs {
			opacity: 0.3;
			pointer-events: none;
		}

		.gutenify-plugin-templates-modal-loading-spinner {
			position: absolute;
			right: 50px;
			top: 14px;
			z-index: 10;

			.components-spinner {
				float: none;
				margin-left: 0;
			}
		}
	}

	// row.
	.gutenify-plugin-templates-categories-row {
	    align-items: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 25px 10px 15px;
	}

	// categories.
	.gutenify-plugin-templates-categories-select {
		margin-bottom: 10px;
		max-width: 170px;
	}

	// count.
	.gutenify-plugin-templates-count {
		align-items: center;
		display: flex;
		flex-direction: revert;
		gap: 20px;
		margin-bottom: 20px;
		margin-top: -50px;
		max-width: 262px;
		// max-width: 170px;
		opacity: 0.7;
	}

	// items background.
	.components-tab-panel__tab-content {
		margin: -15px -16px;
		padding: 15px 16px;

		// background-color: $light-gray-100;
	}
}

// missing blocks.
.gutenify-templates-missing-block-alert.gutenify-alert {
	border-left-color: var(--gkt-color-danger);
	margin-bottom: 20px;

	.gutenify-templates-missing-block-additional {
		width: 100%;
	}

	.gutenify-templates-missing-block-additional,
	.gutenify-templates-missing-block-additional .components-button {
		margin-top: 10px;
	}
}

.gutenify-plugin-templates-spinner .components-spinner {
	float: none;
	margin-left: 0;
}

.gutenify-plugin-templates-list {
	display: flex;
	flex-wrap: wrap;
	list-style: none;
	margin-left: -15px;
	margin-top: -15px;
	padding: 0;

	> .gutenify-plugin-templates-list-item {
		flex: 0 0 25%;
		padding-left: 30px;
		padding-top: 20px;
		width: 25%;

		@media screen and (max-width: 840px) {
			flex: 0 0 50%;
			width: 50%;
		}
		@media screen and (max-width: 450px) {
			flex: 0 0 100%;
			width: 100%;
		}

		&-pro {
			> button {
				&::after {
					content: "Pro";
					background: #4caf50;
				}
			}
		}

		&-free {
			> button {
				&::after {
					content: "Free";
					background: #fc5246;
				}
			}
		}
		.gutenify-plugin-templates-list-item-title {
			button {
				float: right;
				cursor: pointer;
				background: #fff;
				border: 2px solid #ffffff;
				color: #000000;
				text-transform: uppercase;
				font-size: 13px;
				padding: 3px 10px;
				border-radius: 22px;
				box-shadow: 0 0 21px #00000021;
				&.gutenify-template-import-button {
				    background: #4caf50;
					color: #fff;
					padding: 5px 6px;
					margin-left: 10px;
					margin-top: -3px;
					border: none;
				}
			}

		}
		> button {
			background: none;
			background-color: #fff;
			border: none;
			border-radius: 0;
			box-shadow: 0 0 17px #dbdbdbad;
			cursor: pointer;
			display: block;
			padding: 0;
			transition: 0.2s border-color, 0.2s box-shadow;
			width: 100%;
			position: relative;
			padding: 15px;
			overflow: hidden;
			&:hover,
			&:focus,
			&:active {
				// box-shadow: 0 0 0 2px $blue-medium-focus;
			}

			&::after {
			    padding: 4px 35px;
				color: #fff;
				position: absolute;
				left: -25px;
				top: 9px;
				z-index: 1;
				box-shadow: 0 0 16px #00000036;
				display: inline-block;
				font-size: 14px;
				transform: rotate(-45deg);
			}

			.gutenify-plugin-templates-list-item-image {
				display: block;
				position: relative;

				img {
					border-top-left-radius: 4px;
					border-top-right-radius: 4px;
					display: block;
					height: auto;
					width: 100%;
				}

				.gutenify-plugin-templates-list-item-image-sizer {
					width: 100%;

					+ img,
					+ .lazyload-wrapper img {
						height: 100%;
						left: 0;
						position: absolute;
						top: 0;
					}
				}
			}
		}

		.gutenify-plugin-templates-list-item-title {
			background: #f1f1f1;
			color: #000;
			font-size: 15px;
			font-weight: 600;
			padding: 16px 20px;
			text-transform: capitalize;
		}

		&.gutenify-plugin-templates-list-item-no-thumb > button {
			padding: 20px 10px;
			text-align: center;
			// background-color: $light-gray-200;
			// border: 1px solid $light-gray-300;
		}
	}
}

// Add Template Button
.gutenify-toolbar-templates button.components-button.components-icon-button {
    background: #4caf50;
    color: #fff;
	span {
		font-size: 14px;
		margin-top: 5px;
	}
}
// Template Demo wrapper

.gutenify-template-demo-wrapper {
	.gutenify-template-demo-sidebar {
	    display: grid;
    justify-content: space-evenly;
    grid-template-columns: auto 1fr auto;
    background: #f9f9f9;
    padding: 20px 30px;
    position: fixed;
    width: 99%;
    box-shadow: 0 0 23px #0000001c;
    left: 0;
    top: 50px;

	}
	.gutenify-template-demo-title {
		text-align: center;
		font-size: 1.5rem;
		color: #000;
		font-weight: 600;
	}
	.gutenify-template-demo-back-btn,
	.gutenify-template-import-button {
		background: #4caf50;
		border: none;
		color: #fff;
		padding: 5px 15px;
		cursor: pointer;
		span {
			font-size: 13px;
			line-height: 1.3;
			padding-top: 0;
		}
	}
	.gutenify-template-import-button span {
		font-size: 15px;
	}
	.gutenify-template-demo-content iframe {
		max-height: 100%;
		height: 900px;
	}
	.gutenify-template-demo-content {
		margin-top: 60px;
	}
}
