// Styles here for accordion.
.gutenify-accordion-wrapper {
	.gutenify-accordion-item {
		margin-bottom: 10px;
		.gutenify-accordion-item-heading {
			background: #f1f0f0;
			border: 1px solid #f1f0f0;
			border-radius: 0;
			cursor: pointer;
			display: flex;
			margin-bottom: -1px;
			padding: 8px 10px;
			text-align: left;
			width: 100%;
			&:hover {
				background: #ddd;
			}
		}
		.gutenify-accordion-item-content {
			background: #fff;
			border: 1px solid #e0e0e0;
			padding: 10px;
		}
		.gutenify-accordion-item-heading-arrow {
			span {
				font-size: 12px;
				margin-right: 5px;
				padding-top: 3px;
			}
		}
		&:last-child {
			margin: 0;
		}
	}
}

.gutenify-accordion-item-content {
	display: none;
}

.gutenify-accordion-item-active {
	.gutenify-accordion-item-content {
		display: block;
	}
}
