
@mixin team-member-block-wrapper-style {
	display: grid;
	grid-gap: 30px;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	max-width: 100%;
	justify-content: center;
}
@mixin gutenify-info-box-block-layout_1-style {
	display: flex;
	flex-direction: column;
}
@mixin gutenify-info-box-block-layout_2-style {
	display: flex;
}
@mixin team-member-block-wrapper-section-title-style {
	font-size: 38px;
	margin-bottom: 30px;
}

/*
* Media
*/

@mixin wp-block-gutenify-team-member-media {
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}
