@mixin testimonial-item-content-wrapper {
	padding: 40px;
}
@mixin gutenify-testimonial-block-grid-wrapper-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"testimonial-thumb"
		"testimonial-content"
		"testimonial-full-name"
		"testimonial-degination"
		"testimonial-rating";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}

@mixin gutenify-testimonial-block-grid-wrapper-layout-two-style {
	grid-template-areas:
		"testimonial-thumb . testimonial-full-name"
		"testimonial-thumb . testimonial-degination"
		"testimonial-thumb . testimonial-content"
		"testimonial-thumb . testimonial-rating";
	grid-template-columns: auto 30px  auto;
}
@mixin gutenify-testimonial-block-grid-wrapper-layout-three-style {
	grid-template-areas:
		"testimonial-thumb . testimonial-full-name testimonial-full-name"
		"testimonial-thumb . testimonial-degination testimonial-degination"
		"testimonial-content testimonial-content testimonial-content testimonial-content"
		"testimonial-rating testimonial-rating testimonial-rating testimonial-rating";
	grid-template-columns: auto 30px 1fr 1fr;
	grid-template-rows: auto 1fr auto auto;
}
@mixin gutenify-testimonial-block-grid-wrapper-layout-four-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"testimonial-thumb"
		"testimonial-full-name"
		"testimonial-degination"
		"testimonial-content"
		"testimonial-rating";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}
@mixin gutenify-testimonial-block-grid-wrapper-layout-five-style {
	align-content: start;
	display: grid;
	grid-template-areas:
		"testimonial-thumb"
		"testimonial-rating"
		"testimonial-content"
		"testimonial-full-name"
		"testimonial-degination";
	grid-template-columns: 100%;
	grid-template-rows: auto;
}
