@import "./mixin";

[data-type="gutenify/testimonial"] {
	.wp-block-gutenify-testimonial {
		& > .gutenify-testimonial-section {
			.block-editor-block-list__layout {
				@include testimonial-item-content-wrapper;
				@include gutenify-testimonial-block-grid-wrapper-style;
				.wp-block:first-child {
					grid-area: testimonial-thumb;
					margin: 0 0 15px;
					&:empty {
						display: block;
						height: 0;
						line-height: 0;
						margin: 0;
						padding: 0;
					}
				}
				.gutenify-testimonial-block-content {
					color: inherit;
					grid-area: testimonial-content;
					margin: 0 0 15px;
					&:empty {
						display: block;
						height: 0;
						line-height: 0;
						margin: 0;
						padding: 0;
					}
				}
				.gutenify-testimonial-block-full-name {
					grid-area: testimonial-full-name;
					margin: 0 0 10px;
					&:empty {
						display: block;
						height: 0;
						line-height: 0;
						margin: 0;
						padding: 0;
					}
				}
				.gutenify-testimonial-block-degination {
					color: inherit;
					font-weight: 500;
					grid-area: testimonial-degination;
					margin: 0 0 15px;
				}
				[data-type="gutenify/star-rating"] {
					grid-area: testimonial-rating;
					margin: 0;
					&:empty {
						display: block;
						height: 0;
						line-height: 0;
						margin: 0;
						padding: 0;
					}
				}
			}
		}
		&.gutenify-testimonial-layout-2 {
			& > .gutenify-testimonial-section {
				.block-editor-block-list__layout {
					@include gutenify-testimonial-block-grid-wrapper-layout-two-style;
					.gutenify-testimonial-block-full-name {
						margin: 10px 0 10px;
					}
				}
			}
		}
		&.gutenify-testimonial-layout-3 {
			& > .gutenify-testimonial-section {
				.block-editor-block-list__layout {
					@include gutenify-testimonial-block-grid-wrapper-layout-three-style;
					.gutenify-testimonial-block-full-name {
						margin: 10px 0 10px;
					}
					.wp-block:first-child {
						margin: 0;
					}
				}
			}
		}
		&.gutenify-testimonial-layout-4 {
			& > .gutenify-testimonial-section {
				.block-editor-block-list__layout {
					@include gutenify-testimonial-block-grid-wrapper-layout-four-style;
					.gutenify-testimonial-block-full-name {
						margin: 10px 0 10px;
					}
				}
			}
		}
		&.gutenify-testimonial-layout-5 {
			& > .gutenify-testimonial-section {
				.block-editor-block-list__layout {
					@include gutenify-testimonial-block-grid-wrapper-layout-five-style;
					.gutenify-testimonial-block-full-name {
						margin: 0;
					}
					[data-type="gutenify/star-rating"] {
						margin-bottom: 15px;
					}
				}
			}
		}
	}
}

.gutenify-testimonial-block-degination {
	font-size: 1rem;
}
