@import "./mixin";

[data-type="gutenify/team-member"] {
	.wp-block-gutenify-team-member {
		padding: 40px;
		.wp-block-image {
			margin: -40px -40px 30px;
		}
		.wp-block-social-links {
			gap: 10px;
			margin: 0;
		}
		& > .gutenify-team-member-section {
			& > .block-editor-inner-blocks {
				& > .block-editor-block-list__layout {
					@include gutenify-team-member-block-grid-wrapper-style;
					& > .wp-block:first-child {
						grid-area: team-member-thumb;
						&:empty {
							display: block;
							height: 0;
							line-height: 0;
							margin: 0;
							padding: 0;
						}
					}
					.gutenify-team-member-block-bio {
						grid-area: team-member-bio;
						margin: 0 0 15px;
						&:empty {
							display: block;
							height: 0;
							line-height: 0;
							margin: 0;
							padding: 0;
						}
					}
					.gutenify-team-member-block-name {
						grid-area: team-member-full-name;
						margin: 0 0 10px;
						&:empty {
							display: block;
							height: 0;
							line-height: 0;
							margin: 0;
							padding: 0;
						}
					}
					.gutenify-team-member-block-degination {
						color: inherit;
						font-weight: 500;
						grid-area: team-member-degination;
						margin: 0 0 15px;
						font-size: 1rem;
						&:empty {
							display: block;
							height: 0;
							line-height: 0;
							margin: 0;
							padding: 0;
						}
					}
					.wp-block:last-child {
						grid-area: team-member-social-links;
						margin: 0;
						&:empty {
							display: block;
							height: 0;
							line-height: 0;
							margin: 0;
							padding: 0;
						}
					}
				}
			}
		}
		&.gutenify-team-member-layout-2 {
			padding: 30px;
			& > .gutenify-team-member-section {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						@include gutenify-team-member-block-grid-wrapper-layout-two-style;
						.gutenify-team-member-block-full-name {
							margin: 10px 0 10px;
						}
						.wp-block:first-child {
							margin: 0;
						}
						.wp-block-image {
							margin: -30px 0 -30px -30px;
						}
					}
				}
			}
		}
		&.gutenify-team-member-layout-3 {
			padding: 30px;
			& > .gutenify-team-member-section {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						@include gutenify-team-member-block-grid-wrapper-layout-three-style;
						.gutenify-team-member-block-full-name {
							margin: 10px 0 10px;
						}
						.wp-block:first-child {
							margin: 0;
						}
						.wp-block-image {
							margin: -30px -30px -30px 0;
						}
					}
				}
			}
		}
		&.gutenify-team-member-layout-4 {
			& > .gutenify-team-member-section {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						align-content: space-around;
						display: grid;
						grid-template-areas:
							"team-member-thumb team-member-thumb"
							"team-member-social-links team-member-full-name"
							"team-member-social-links team-member-degination"
							"team-member-social-links team-member-bio";
						grid-template-columns: auto;
						grid-template-rows: auto;
						column-gap: 20px;
						.wp-block-social-links {
							gap: 10px;
							font-size: inherit;
						}
					}
				}
			}
		}
		&.gutenify-team-member-layout-5 {
			& > .gutenify-team-member-section {
				& > .block-editor-inner-blocks {
					& > .block-editor-block-list__layout {
						align-content: space-around;
						display: grid;
						grid-template-areas:
						"team-member-thumb"
						"team-member-social-links"
						"team-member-full-name"
						"team-member-degination"
						"team-member-bio";
						grid-template-columns: auto;
						grid-template-rows: auto;
						column-gap: 20px;
						.wp-block-social-links {
							gap: 10px;
							font-size: inherit;
							margin-bottom: 25px;
						}
					}
				}
			}
		}
	}
}
